
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2{
  font-family: Playfair Display;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 130%;
  display: flex;
  align-items: flex-end;
  letter-spacing: -0.02em;
  color: #1C1C21;
  text-align:left;
  }

/* Manipulates Bootstrap grid to be consistent according to Feedback. */

.container-fluid-zooma {
  display: flex !important;
  margin-left:auto;
  margin-right:auto;
}

.row > .col,
    .row > [class*="col-"] {
      padding-right: 16px;
      padding-left: 16px;
    }

    .row {
      display: flex;
      flex-wrap: wrap;
      margin-right: 0px !important;
      margin-left: 0px !important;
      width:100%;
  }

.nav-container {
  position: absolute;
  width: 100%;
}

.nav-menu-center {
  position: absolute;
  left: 0;
  right: 0;
}

/* logo*/
.navbar-brand {
  margin-left: 30px;
}

.nav-logo {
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 16px !important;
  line-height: 56px !important;
  display: flex;
  align-items: center;
  letter-spacing: 0.2em;
  color: #E5E5E5;
}



/* menu mobile */

.navbar-toggler {
  border: 0px !important;
}


.navbar-toggler-icon {
display:none
}

.navbar-toggler-icon_sk {
  color: rgb(208, 138, 32) !important;
  background-image: url("menu.svg") !important;
  display: inline-block;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    content: "";
    background: no-repeat center center;
    background-size: 100% 100%;
    margin-right: 25px;
}


.navbar-toggler-icon_sk-close {
  color: rgb(208, 138, 32) !important;
  background-image: url("close.svg") !important;
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  content: "";
  background: no-repeat center center;
  background-size: 100% 100%;
  margin-right: 25px;
}

.nav-mob {
  padding-top: 30px;
  padding-bottom: 30px;
  margin-left: -15px !important;
  margin-right: -15px !important;
  margin-bottom: -8px !important;
  border-top:1px solid #ffffff1c !important;
  border-bottom:1px solid #ffffff1c !important;
  background-color: #1C1C21 !important;
  margin-top:-2px;
}

button:focus {
  outline: none !important;
}

.nav-menu-item-sm {
  padding: 20px 25px 20px 25px !important;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #E5E5E5;
}

li.nav-item.nav-li-sm a:hover {
  border-bottom:0px !important;
}

/* menu tablet */

.nav-background-tablet {
  background-color: #1C1C21 !important;
  height:45px;
  border-top:1px solid #ffffff1c !important;
}

.navbar {
  padding:0px !important;
}

.nav-li-md {
  height:45px;
  justify-content: center
}

li.nav-item-md a:hover {
  height:45px;
  color: rgb(208, 138, 32) !important;
  border-bottom:1px solid rgb(208, 138, 32) !important;
}

.nav-menu-item-md span {
  display: inline-block !important;  
  vertical-align: middle !important;   
  line-height: 45px !important;  
}

.nav-menu-item-md {
  padding: 0px 25px 0 25px !important;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #E5E5E5;
}


/* menu desktop */

.nav-menu-lg-full-width {
  width:100%;
}

.nav-background-lg {
  background-color: #1C1C21 !important;
  height:64px;
}
.navbar-expand-md .navbar-nav {
  justify-content: center !important;;
}

.nav-li-lg {
  height:64px;
  justify-content: center
}

li.nav-item a:hover {
  color: rgb(208, 138, 32) !important;
  border-bottom:1px solid rgb(208, 138, 32) !important;
}

ul li.nav-item span {
  display: inline-block;  
  vertical-align: middle;  
  line-height: 64px; 
}

.nav-menu-item-lg {
  padding: 0px 25px 0 25px !important;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #E5E5E5;
}



/* language */
.nav-menu-item-lang{
  padding: 0px 4px 0px 4px !important;
}


/* contact */

.navbar-nav .nav-menu-item-contact a:hover {
  color: #1C1C21 !important;
}

.nav-menu-item-contact{
  background: #D28C00;
  color: #1C1C21 !important;
  padding: 15px !important;
  margin-right:30px;
  margin-left:30px;
  margin-top:0px;
  font-family: Lato;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color:black;
}

li.nav-item.nav-li-lg-contact{
  display: inline-block;  
  vertical-align: middle;  
  line-height: 12px;
}

li.nav-item.nav-li-lg-contact a:hover {
  color: black !important;
  border-bottom:0px !important;
  background-color:#FFB92C;
}



/* Header */


.hero-title {
  font-family: Playfair Display;
  font-style: italic;
  font-weight: normal;
  font-size: 72px;
  line-height: 120%;
  display: flex;
  align-items: flex-end;
  letter-spacing: -0.02em;
  color: #FFFFFF;
  width: 736px;
  text-align: left;

  
}

/* Intro */

h2.intro {
font-family: Playfair Display;
font-style: normal;
font-weight: normal;
font-size: 48px;
line-height: 110%;
display: flex;
align-items: flex-end;
letter-spacing: -0.02em;
color: #1C1C21;
}

p.intro {
font-family: Lato;
font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 160%;
color: #6A6464;
text-align:left;
}

/* Intro */

.blog-image {
height:218px;
}

/* BLOG */

.blog-section {
background: #F2F2F2;
}

.blog-row{
padding-top:70px;
padding-bottom:70px;
}

.blog-no-padding-left {
padding-left:0px !important;
}

.blog-no-padding-right {
  padding-right:0px !important;
  }
  

.card-blog{
  border:0px !important;
  border-radius:0px !important;
  padding: 0px !important;
  background:none !important;
}


h3.blog {
font-family: Lato;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 140%;
color: #1C1C21;
text-align:left;
margin-top:30px;
text-transform:uppercase;
}

p.blog {
font-family: Lato;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 160%;
color: #6A6464;
text-align:left;
}

.blog-space{
  height:50px;
}

.button_arrow {
  height: 30px ;
  margin-right: 12px;
}

.blog-read-more {
  text-align: left;
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 100%;
  color: #D28C00;
  padding-top: 14px;
  display: block;
  position: absolute;
  bottom:0px;
  padding-bottom:20px;
}


  
.blog-read-more a{
  color: #d08a20;
}

.blog-read-more a:hover{
  color: #d08a20;
}
  
/* LAST SECTION */

.last-section-image{
  width:100%;
  height:600px;
  position: relative;
}
  
.last-section-fill{
width:100%;
height:600px;
position: absolute;
  top: 0;
  left: 0;
}

.last-section-container {
  margin-top:-600px;
  background:#dbdbdb;
}

.last-w {
margin-bottom:153px;
}

.last-section-overlay{
  width:100%;
  background-color:white;
  height:600px;
  
}

.last-section-text-div{
  height:600px;
  background-color:#dbdbdb;
  padding-top:120px;
}

.last-section-diagonal{
  background-image: url("trekanten2.svg");
    min-height: 100%;
    background-position: 0;
    background-repeat: no-repeat;
}

.last-section-bg {
  background-color: #dbdbdb!important

}
  

